import React from "react";
import PropTypes from "prop-types";

// Define badge color variants grouped by collections
const BADGE_COLORS = {
  // Basic variants
  BASIC: ["primary", "secondary", "basic"],
  // Status variants
  STATUS: ["success", "warning", "info", "error"],
  // Lebanese-inspired collections
  CEDAR: ["cedar", "cedar-light", "cedar-dark"],
  MEDITERRANEAN: ["sea", "coast", "azure"],
  MOUNTAIN: ["stone", "earth", "terracotta"],
  BEIRUT: ["urban", "concrete", "steel"],
  CULINARY: ["olive", "saffron", "grape"],
  FESTIVAL: ["festive", "celebration", "heritage"],
  CRAFT: ["copper", "silver", "ceramic"],
  MODERN: ["tech", "digital", "startup"],
};

// Define badge sizes
const BADGE_SIZES = {
  SMALL: "small",
  MEDIUM: "medium",
  LARGE: "large",
  HUGE: "huge",
};

// Define badge radius options
const BADGE_RADIUS = {
  NONE: "none",
  SMALL: "small",
  MEDIUM: "medium",
  LARGE: "large",
  PILL: "pill",
};

// Define badge variants
const BADGE_VARIANTS = {
  NORMAL: "normal",
  OUTLINED: "outlined",
  LIGHT: "light",
};

// Flatten variants for PropTypes validation
const ALL_COLORS = Object.values(BADGE_COLORS).flat();
const ALL_SIZES = Object.values(BADGE_SIZES);
const ALL_RADIUS = Object.values(BADGE_RADIUS);
const ALL_VARIANTS = Object.values(BADGE_VARIANTS);

// Helper to get collection name from color
const getCollectionFromColor = (color) => {
  return (
    Object.entries(BADGE_COLORS)
      .find(([_, colors]) => colors.includes(color))?.[0]
      ?.toLowerCase() || "basic"
  );
};

// Helper to determine if a color is light or dark
const isLightColor = (hexColor) => {
  // Remove the hash if it exists
  hexColor = hexColor.replace("#", "");

  // Convert to RGB
  let r, g, b;
  if (hexColor.length === 3) {
    r = parseInt(hexColor[0] + hexColor[0], 16);
    g = parseInt(hexColor[1] + hexColor[1], 16);
    b = parseInt(hexColor[2] + hexColor[2], 16);
  } else {
    r = parseInt(hexColor.substr(0, 2), 16);
    g = parseInt(hexColor.substr(2, 2), 16);
    b = parseInt(hexColor.substr(4, 2), 16);
  }

  // Calculate luminance
  const luminance = (0.299 * r + 0.587 * g + 0.114 * b) / 255;

  // Return true if light, false if dark
  return luminance > 0.5;
};

// Helper to generate a lighter version of a color for background
const generateLightBackground = (hexColor, opacity = 0.15) => {
  // Remove the hash if it exists
  hexColor = hexColor.replace("#", "");

  // Convert to RGB
  let r, g, b;
  if (hexColor.length === 3) {
    r = parseInt(hexColor[0] + hexColor[0], 16);
    g = parseInt(hexColor[1] + hexColor[1], 16);
    b = parseInt(hexColor[2] + hexColor[2], 16);
  } else {
    r = parseInt(hexColor.substr(0, 2), 16);
    g = parseInt(hexColor.substr(2, 2), 16);
    b = parseInt(hexColor.substr(4, 2), 16);
  }

  // Return rgba color with opacity
  return `rgba(${r}, ${g}, ${b}, ${opacity})`;
};

// Helper to generate a darker version of a color for text
const generateDarkerColor = (hexColor) => {
  // Remove the hash if it exists
  hexColor = hexColor.replace("#", "");

  // Convert to RGB
  let r, g, b;
  if (hexColor.length === 3) {
    r = parseInt(hexColor[0] + hexColor[0], 16);
    g = parseInt(hexColor[1] + hexColor[1], 16);
    b = parseInt(hexColor[2] + hexColor[2], 16);
  } else {
    r = parseInt(hexColor.substr(0, 2), 16);
    g = parseInt(hexColor.substr(2, 2), 16);
    b = parseInt(hexColor.substr(4, 2), 16);
  }

  // Make darker by reducing each component by 50% for even more contrast
  r = Math.max(0, Math.floor(r * 0.5));
  g = Math.max(0, Math.floor(g * 0.5));
  b = Math.max(0, Math.floor(b * 0.5));

  // Convert back to hex
  return `#${r.toString(16).padStart(2, "0")}${g
    .toString(16)
    .padStart(2, "0")}${b.toString(16).padStart(2, "0")}`;
};

// Main Badge component
const Badge = ({
  children,
  color = "primary",
  size = BADGE_SIZES.MEDIUM,
  radius = BADGE_RADIUS.MEDIUM,
  variant = BADGE_VARIANTS.NORMAL,
  outlineColor,
  outlineTextColor,
  propColor,
  isBordered = true,
  icon,
  iconPosition = "start",
  iconSpacing = 7,
  onClick,
  interactive = false,
  className = "",
  style = {},
  ...props
}) => {
  // Build class names
  let badgeClass = `
    lebify-badge 
    lebify-badge-${size}
    lebify-badge-radius-${radius}
    ${variant !== "normal" ? `lebify-badge-${variant}` : ""}
    ${interactive || onClick ? "lebify-badge-interactive" : ""}
    ${className}
  `
    .trim()
    .replace(/\s+/g, " ");

  // Only add color class if not using propColor
  if (!(variant === "light" && propColor)) {
    badgeClass += ` lebify-badge-${color}`;
  }

  // Build custom styles
  const customStyle = { ...style };

  // Add cursor pointer for interactive badges
  if (interactive || onClick) {
    customStyle.cursor = "pointer";
  }

  // Handle outlined variant custom styles
  if (variant === "outlined") {
    if (outlineColor) {
      customStyle.borderColor = outlineColor;
    }
    if (outlineTextColor) {
      customStyle.color = outlineTextColor;
    }
  }

  // Handle light variant with propColor
  if (variant === "light" && propColor) {
    // Override any existing background and color styles
    customStyle.backgroundColor = generateLightBackground(propColor, 0.18);
    customStyle.color = generateDarkerColor(propColor);

    // Ensure medium font weight
    customStyle.fontWeight = "var(--lebify-font-weight-medium, 500)";

    // Add a subtle border for better definition
    customStyle.border = `1px solid ${generateLightBackground(propColor, 0.4)}`;

    // Add box-shadow for more depth
    customStyle.boxShadow = `inset 0 0 0 1px ${generateLightBackground(
      propColor,
      0.1
    )}`;

    // Adjust opacity for dark mode
    if (
      window.matchMedia &&
      window.matchMedia("(prefers-color-scheme: dark)").matches
    ) {
      customStyle.backgroundColor = generateLightBackground(propColor, 0.25);
      customStyle.color = propColor;
      customStyle.border = `1px solid ${generateLightBackground(
        propColor,
        0.5
      )}`;
    }

    // Check if we're in a theme context
    const dataTheme = document.documentElement.getAttribute("data-theme");
    if (dataTheme === "dark") {
      customStyle.backgroundColor = generateLightBackground(propColor, 0.25);
      customStyle.color = propColor;
      customStyle.border = `1px solid ${generateLightBackground(
        propColor,
        0.5
      )}`;
    }
  }

  // Handle click event
  const handleClick = (e) => {
    if (onClick) {
      onClick(e);
    }
  };

  // Render the badge with icon if provided
  if (icon) {
    const iconStyle = {
      display: "inline-flex",
      alignItems: "center",
      marginRight: iconPosition === "start" ? iconSpacing : 0,
      marginLeft: iconPosition === "end" ? iconSpacing : 0,
    };

    return (
      <span
        className={badgeClass}
        style={customStyle}
        onClick={handleClick}
        role={onClick || interactive ? "button" : undefined}
        tabIndex={onClick || interactive ? 0 : undefined}
        {...props}
      >
        {iconPosition === "start" && <span style={iconStyle}>{icon}</span>}
        {children}
        {iconPosition === "end" && <span style={iconStyle}>{icon}</span>}
      </span>
    );
  }

  // Render the badge without icon
  return (
    <span
      className={badgeClass}
      style={customStyle}
      onClick={handleClick}
      role={onClick || interactive ? "button" : undefined}
      tabIndex={onClick || interactive ? 0 : undefined}
      {...props}
    >
      {children}
    </span>
  );
};

Badge.propTypes = {
  children: PropTypes.node,
  color: PropTypes.oneOf(ALL_COLORS),
  size: PropTypes.oneOf(ALL_SIZES),
  radius: PropTypes.oneOf(ALL_RADIUS),
  variant: PropTypes.oneOf(ALL_VARIANTS),
  outlineColor: PropTypes.string,
  outlineTextColor: PropTypes.string,
  propColor: PropTypes.string,
  isBordered: PropTypes.bool,
  icon: PropTypes.node,
  iconPosition: PropTypes.oneOf(["start", "end"]),
  iconSpacing: PropTypes.number,
  onClick: PropTypes.func,
  interactive: PropTypes.bool,
  className: PropTypes.string,
  style: PropTypes.object,
};

// Export the variants for external use
Badge.COLORS = BADGE_COLORS;
Badge.SIZES = BADGE_SIZES;
Badge.RADIUS = BADGE_RADIUS;
Badge.VARIANTS = BADGE_VARIANTS;

export default Badge;
